<template>
  <div>
    <el-card class="box-card login text-center" shadow="always">
      <div slot="header" class="clearfix align-center">
        <span class="font-weight-bold">MPF管理后台</span>
      </div>

      <el-form>
        <el-form-item>
          <el-input
            v-model="username"
            type="text"
            tabindex="1"
            prefix-icon="el-icon-user-solid"
            placeholder="请输入用户名"
            autocomplete="on"
          />
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="password"
            type="password"
            tabindex="2"
            prefix-icon="el-icon-lock"
            placeholder="请输入密码"
            autocomplete="on"
            show-password
          />
        </el-form-item>
        <el-form-item>
          <el-checkbox
            v-model="remember"
            label="30天内自动登陆"
            border
          ></el-checkbox>
          <el-button v-on:click="login" type="primary" class="ml-4">
            登 陆
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      remember: false
    };
  },
  methods: {
    login: function() {
      this.axios.post("/api?ctr=&action=");
    }
  }
};
</script>

<style>
.login {
  position: absolute;
  top: 20%;
  left: 50%;
  width: 300px;
  margin-left: -150px;
}
</style>
